import React, { useState, useEffect } from 'react'

import './css/Index.css'


import { Link, NavLink } from 'react-router-dom'


import { format } from 'date-fns';
import { DayPicker } from 'react-day-picker';
import 'react-day-picker/dist/style.css';

export default function ReservatonInfo() {
    const [selected, setSelected] = React.useState(new Date());

    let footer;
    // let footer = <p>Please pick a day.</p>;
    // if (selected) {
    //     footer = <p>You picked {format(selected, 'PP')}.</p>;
    // }

    const [counter, setCount] = useState(0);
    const handleIncrement = () => {
        setCount(prevCount => prevCount + 1);
    };

    const handleDecrement = () => {
        setCount(prevCount => (prevCount > 0 ? prevCount - 1 : 0));
    };


    return (
        <div className='wrap'>
            <div className='animated-title'>
                <div className='track'>
                    <p className='content'>
                             100%   ,    
                          . *      .
                    </p>
                </div>
            </div>

            <header className="res-header">
                <div className='res-header_inner'>
                    <Link to={'/'}>
                        <a href="/">
                            <div id='logo'>
                                <img src={`${process.env.PUBLIC_URL}/assets/img/logo2.png`} alt="" />
                            </div>
                        </a>
                    </Link>
                    <nav className='menu-container'>
                        <ul id='menu'>
                            <li><NavLink to='/reservation' style={{ fontWeight: 500 }}></NavLink></li>
                            <li><NavLink to='/reservationlist' style={{ color: "#9C9C9C" }}></NavLink></li>
                        </ul>
                    </nav>
                    <div className='sub-menu'>
                        <ul>
                            <li className='checkart'><a href=""><span></span></a></li>
                            <li><a href=""><span style={{ fontWeight: "bolder" }}>KOR</span></a></li>
                        </ul>
                    </div>
                </div>
            </header>

            <main id='ReservationInfo_main'>
                <div className='ReservationInfo_wrap'>
                    <div className='reservationInfo-img'>
                        <div className='reserInfo-ticket'>
                            <img src={`${process.env.PUBLIC_URL}/assets/img/reser01.jpg`} alt="" />
                            <div className='title'>
                                <span>  </span>
                            </div>
                            <hr className='title_hr'/>
                            <div className='place'>
                                <span> -  M2</span>
                            </div>
                            <hr className='info_hr'/>
                            <div className='date'>
                                <span> - {format(selected, 'PP')}</span>
                                {/* <p>You picked {format(selected, 'PP')}.</p> */}
                            </div>
                            <hr className='info_hr'/>
                            <div className='people'>
                                <span> - {counter}</span>
                            </div>
                        </div>
                    </div>
                    <div className='reservationInfo-choice'>
                        <div className='reservationInfo-choice-wrap'>
                            <div className='reservationInfo-choice-left'>
                                <div className='calendar'>
                                    <DayPicker
                                        className='rdp'
                                        mode="single"
                                        selected={selected}
                                        onSelect={setSelected}
                                        footer={footer}
                                    />
                                </div>
                                <div className='counter'>
                                    <div className='counter-span'>
                                        <span></span>
                                    </div>
                                    <div className='counter-box'>
                                        <p>//</p>
                                        <div className='conter-bg'>
                                            <div>{counter}</div>
                                            <div className='counterbtn'>
                                                <button onClick={handleDecrement}>-</button>
                                                <button onClick={handleIncrement} >+</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className='information'>
                                <div className='information-wrap'>
                                    <div className='information-span'>
                                        <span>   </span>
                                    </div>
                                    <p>
                                           14  ,   
                                         <br/> 4 .
                                    </p>
                                    <hr className='hr'/>
                                    <p>
                                               
                                        ,       
                                          .
                                    </p>
                                    <hr className='hr'/>
                                    <p>
                                                , 
                                         18 .
                                    </p>
                                    <hr className='hr'/>
                                    <p>
                                          : WE        ,
                                                 
                                        .
                                    </p>
                                    <hr className='hr'/>
                                    <p>
                                                
                                         .
                                    </p>
                                </div>
                                <div className='information-resbtn'>
                                    <button>
                                        
                                    </button>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </main>
        </div>
    )
}
