import React, { useState, useEffect } from 'react'

import './css/Index.css'

import { Link } from 'react-router-dom'



export default function ProgramInfo() {
    // useState  'prevScrollY' 'visible'  
    // prevScollY :   , visible :      
    const [prevScrollY, setPrevScrollY] = useState(0);
    const [visible, setVisible] = useState(true);

    // useEffect     
    useEffect(() => {
        /* handleScroll      'prevScrollY'  ,
                visible  */
        //    visible  false   
        const handleScroll = () => {
            const currentScrollY = window.scrollY;

            if (prevScrollY < currentScrollY && visible) {
                setVisible(false);
            } else if (prevScrollY > currentScrollY && !visible) {
                setVisible(true);
            }

            setPrevScrollY(currentScrollY);
        };

        //   
        window.addEventListener("scroll", handleScroll);

        //           
        return () => window.removeEventListener("scroll", handleScroll);
    }, [prevScrollY, visible]);

    // 'visible'         
    // true  "header" false  "header header--hidden"   
    const header = visible ? "header" : "header header--hidden";
    return (
        <div className='wrap'>
            <header className={header}>
                <div className='header_inner'>
                    <Link to={'/'}>

                        <div id='logo'>
                            <img src={`${process.env.PUBLIC_URL}/assets/img/logo2.png`} alt="" />
                        </div>

                    </Link>
                    <nav className='menu-container'>
                        <ul id='menu'>
                            <li><Link to={'/'}></Link></li>
                            <li><Link to={'/'}></Link></li>
                            <li><Link to={'/'}></Link></li>
                            <li><Link to={'/reservation'}></Link></li>
                        </ul>
                    </nav>
                    <div className='sub-menu'>
                        <ul>
                            <li className='checkart'><a href=""><span></span></a></li>
                            <li><a href=""><span style={{ fontWeight: "bolder" }}>KOR</span></a></li>
                        </ul>
                    </div>
                </div>
            </header>

            <div className='programinfo-wrap'>
                <div className='programinfo-title'>
                    <h3>[MAURIZIO CATTELAN] WE  [  
                        2:  ,    
                        /   ]
                    </h3>
                </div>
                <div className='programinfo-info'>
                    <div className='infoimg'>
                        <img src={`${process.env.PUBLIC_URL}/assets/img/program02.jpg`} alt="" />
                    </div>
                    <div className='programinfo-text'>
                        <div className='programinfo-text-span'>
                            <span> </span>
                        </div>
                        <p>
                                     
                                 .  ,   
                                   ? [
                              2 :  ,     /
                              ]       
                                ,      
                                  .
                        </p>
                        <ul className='programinfo-list'>
                            <li>: 2023.04.26() 16:00 - 17:30</li>
                            <li>:  </li>
                            <li>: </li>
                            <li>: 160</li>
                            <li>: </li>
                            <li>: 2023. 4 .12() 14:00      <br />
                                *[]   :<br />
                                4. 11.() 14:00  20<br />
                                    </li>
                        </ul>
                    </div>
                    <button className='program-btn'>
                        
                        <img className='btnimg' src={`${process.env.PUBLIC_URL}/assets/img/heartbtn.png`} alt="" />
                    </button>
                </div>
            </div>
        </div>
    )
}
